import { memo, useCallback, useEffect, useMemo, useRef } from 'react';
import { I_Hotspot } from 'containers/Home/types';
import FancyboxContainer, { Type_RefFuncFancybox } from '@components/Fancybox';

interface Props {
  hotspotSelected: I_Hotspot;
  onClose: () => void;
}

const Fancybox = memo(({ hotspotSelected, onClose }: Props) => {
  const fancyboxRef = useRef<Type_RefFuncFancybox>(null!);

  const options = useMemo(
    () => ({
      infinite: false,
      closeButton: 'outside',
      dragToClose: false,
      helpers: {
        overlay: { closeClick: false },
      },
      clickSlide: false,
      touch: false,
      on: {
        destroy: () => onClose(),
      },
    }),
    [onClose],
  );

  const renderContent = useCallback(() => {
    switch (hotspotSelected.type) {
      case 'image':
        fancyboxRef.current?.getRoot().show(
          hotspotSelected.config?.image?.galleries?.map(item => ({
            src: item.src,
            type: 'image',
          })),
          options,
        );
        break;
      case 'video':
        fancyboxRef.current?.getRoot().show(
          [
            {
              src: hotspotSelected.config?.video?.url,
              type: 'video',
            },
          ],
          options,
        );
        break;
      case 'media':
        switch (hotspotSelected.config?.media?.type) {
          case 'media-video':
            switch (hotspotSelected.config?.media?.action) {
              case 'zoom':
                fancyboxRef.current?.getRoot().show(
                  [
                    {
                      src: hotspotSelected.config?.media?.media?.src,
                      type: 'video',
                    },
                  ],
                  options,
                );
                break;
              default:
                break;
            }
            break;
          case 'media-image':
            switch (hotspotSelected.config?.media?.action) {
              case 'zoom':
                // case undefined:
                // case '':
                fancyboxRef.current?.getRoot().show(
                  [
                    {
                      src: hotspotSelected.config?.media?.media?.src,
                      type: 'image',
                    },
                  ],
                  options,
                );
                break;
              default:
                break;
            }
            break;
          default:
            break;
        }

      case 'link':
        if (hotspotSelected.config?.link?.action === 'iframe') {
          fancyboxRef.current?.getRoot().show(
            [
              {
                src: hotspotSelected.config?.link?.url,
                type: 'iframe',
              },
            ],
            options,
          );
        }
        break;
      default:
        break;
    }
  }, [hotspotSelected, options]);

  useEffect(() => {
    renderContent();
  }, [renderContent]);

  return (
    <FancyboxContainer
      ref={fancyboxRef}
      options={{
        on: {
          destroy: () => onClose(),
        },
      }}
    >
      <a
        data-fancybox="gallery"
        href="https://lipsum.app/id/3/800x600"
        className="el-hide"
      >
        Panoee
      </a>
    </FancyboxContainer>
  );
});

export default Fancybox;
